html {
  background-color: #555;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  min-width: 850px;
  width: 850px;
  max-width: 850px;
  margin: 0 auto;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  row-gap: 50px;
}

button {
  width: 50px;
  height: 100%;
  flex-shrink: 0;
  flex-grow: 0;
  border: none;
  border-radius: 6px;
  background-color: #212B36;
  color: inherit;
  box-shadow: 0 5px #000;
  transition: background-color linear 0.1s, box-shadow linear 0.1s;
}

button:active {
  background-color: #000;
  box-shadow: 0 5px #fff;
}

.active {
  background-color: #000;
  box-shadow: 0 5px #fff;
}

.keyboard {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  row-gap: 10px;
  padding: 10px;
  background-color: grey;
}

.keyboard-row {
  display: flex;
  column-gap: 5px;
  height: 50px;
}

.output {
  width: 100%;
  min-height: 300px;
}

.flex-grow {
  flex-grow: 1;
}

.power-on {
  background-color: aquamarine;
}

.info {
  text-align: center;
  color: black;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}